$choosen-border-color: #333;
$standard-border-colrgb: rgb(54, 194, 189); // в лист
.filter_icon_container {
  background-color: #fff;
  width: 4%;
  z-index: 3;
}
.filter_btn {
  width: 100%;
  padding: 0;
}
.filter_btn svg {
  width: 100%;
  stroke: $choosen-border-color;
}
.filter_container {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  align-items: flex-start;
  padding: 0 1rem;
  background-color: #fff;

  position: absolute;
  left: -100%;
  transition: all 0.2s linear;
  z-index: 2;

  p {
    font-size: 1.2rem;
    font-family: "Bitter", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
  }
}

.filter_sort {
  display: block;
  font-size: 1.07rem;

  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;

  color: #444; //поменять
  line-height: 1.3rem;
  padding: 0.6em 1.4em 0.5em 0.8em;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #a3a3a3;
  border-radius: 0.5rem;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%2336c2bd%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.filter_sort::-ms-expand {
  display: none;
}
.filter_sort:hover {
  border-color: #888;
}
.filter_sort:focus {
  border-color: transparent;
  box-shadow: 0 0 1px 2px $standard-border-colrgb;
  box-shadow: 0 0 0 2px -moz-mac-focusring;
  outline: none;
}
.filter_sort option {
  font-weight: normal;
}

.setFilter_btn {
  margin: 0 auto;
}

.show_filter_container {
  left: 0;
}
